<template>
  <div id="calendar-entry">
    <div class="calendar-entry-note">
      <input v-model="inputEntry" required type="text" placeholder="New Event"/>
      <p class="calendar-entry-day">Day of event: <span class="bold">{{activeDayTitle}}</span></p>
      <p v-if="error" :style="errorStyle">You must type something first!</p>
      <button @click="submitEvent(inputEntry)" class="button is-primary is-small is-outlined">Submit</button>
    </div>
  </div>
</template>

<script>
  import {store} from '../store';

  export default {
    name: "CalendarEntry",
    computed: {
      activeDayTitle() {
        return store.getActiveDay().fullTitle
      }
    },
    data() {
      return {
        inputEntry: "",
        errorStyle: `color: tomato; font-size: 13px;`,
        error: false
      }
    },
    methods: {
      submitEvent(details) {
        if (!this.inputEntry) return this.error = true;
        store.submitEvent(this.inputEntry);
        this.inputEntry = '';
        this.error = false;
      }
    }

  }
</script>

<style lang="scss" scoped>
  #calendar-entry {
    clear: both;
    background: #FFF;
    border: 1px solid #42b883;
    border-radius: 10px;
    max-width: 300px;
    margin: 0 auto;
    padding: 20px;

    .calendar-entry-note {
      input {
        width: 200px;
        font-weight: 600;
        border: 0;
        border-bottom: 1px solid #CCC;
        font-size: 15px;
        height: 30px;
        margin-bottom: 10px;

        &:focus {
          outline: none;
        }
      }

      .submit {
        display: block;
        margin: 0 auto;
      }
    }
  }
</style>



