import {seedData} from "./seed";

export const store = {
  state: {
    days: seedData
  },

  getActiveDay() {
    return this.state.days.find((day) => day.active === true);
  },

  setActiveDay(dayId) {
    this.state.days.forEach(day => {
      day.id === dayId ? day.active = true : day.active = false;
    })
  },

  submitEvent(eventDetails) {
    const activeDay = this.getActiveDay();
    activeDay.events.push({
      eventDetails,
      edit: false
    });
    console.log(activeDay);
  },

  editEvent(dayId, eventId) {
    const day = this.state.days.find((day) => day.id === dayId);
    const event = day.events[eventId];
    event.edit = true;
  },

  updateEvent(dayId, eventIndex, newEventDetails) {
    const day = this.state.days.find((day) => day.id === dayId);
    const event = day.events[eventIndex];
    event.details = newEventDetails;
    event.edit = false;
  },

  removeEvent(dayId, index) {
    const day = this.state.days.find((day) => day.id === dayId);
    day.events.splice(index, 1);
  }
}

export default store;



