<template>
  <div class="day-event" :style="getEventBackgroundColor">
    <div v-if="!event.edit">
      <span class="has-text-centered details">{{event.details}}</span>
      <div class="has-text-centered icons">
        <i @click="editEvent(day.id,index)"
           class="fa fa-pencil-square edit-icon"></i>
        <i @click="removeEvent(day.id, index)" class="fa fa-trash-o delete-icon"></i>
      </div>
    </div>
    <div v-if="event.edit">
      <input v-model="newEventDetails" type="text" :placeholder="event.details">
      <div class="has-text-centered icons">
        <i @click="updateEvent(day.id,index,newEventDetails)" class="fa fa-check"></i>
      </div>
    </div>
  </div>
</template>

<script>
  import {store} from "../store";

  export default {
    name: "CalendarEvent",
    props: ['event', 'day', 'index'],
    data() {
      return {
        newEventDetails: ''
      }
    },
    computed: {
      getEventBackgroundColor() {
        const colors = ['#FF9999', '#85D6FF', '#99FF99'];
        let randomColor = colors[Math.floor(Math.random() * colors.length)];
        return `background-color: ${randomColor}`;
      }
    },
    methods: {
      editEvent(dayId, index) {
        store.editEvent(dayId, index);
      },
      updateEvent(dayId, index, newEventDetails) {
        store.updateEvent(dayId, index, newEventDetails);
        this.newEventDetails = '';
      },
      removeEvent(dayId, index) {
        store.removeEvent(dayId, index);
      }
    }
  }
</script>

<style lang="scss" scoped>
  .day-event {
    margin-top: 6px;
    margin-bottom: 6px;
    display: block;
    color: #4C4C4C;
    padding: 5px;

    .details {
      display: block;
    }

    input {
      background: none;
      border: 0;
      border-bottom: 1px solid #FFF;
      width: 100%;

      &:focus {
        outline: none;
      }
    }
  }
</style>



